.toggle {
  position: relative;
  width: 30px;
  height: 30px;
  border: none;
  padding: 0;
  margin: 0;
  margin-left: auto;
  background-color: transparent;
  cursor: pointer;

  @mixin hover-focus {
    opacity: 0.8;
  }

  @mixin active {
    opacity: 0.3;
  }
}

.toggle__open::before {
  content: "";
  position: absolute;
  top: 7px;
  right: 0;
  width: 24px;
  height: 14px;
  background-image: url("../img/sprite/burger-icon.svg");
}

.is-open .toggle__close::before {
  content: "";
  position: absolute;
  top: 7px;
  right: 0;
  width: 15px;
  height: 15px;
  background-image: url("../img/svg/toggle-close.svg");
  background-position: center;
  z-index: 20;
}
